<template>
  <div class="todo-footer">
    <label>
      <input type="checkbox" v-model="isAll" />
    </label>
    <span>
      <span>已完成{{ doneNumber }}</span> / 全部{{ ItemList.length }}
    </span>
    <button class="btn btn-danger" @click="clear">清除已完成的任务</button>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
export default {
  name: 'Footer',
  computed: {
    ...mapState(['ItemList']),
    doneNumber: {
      get() {
        return this.ItemList.reduce((prev, curr) => {
          return prev + (curr.completed ? 1 : 0)
        }, 0)
      },
    },
    isAll: {
      get() {
        if (this.doneNumber === this.ItemList.length) {
          if (this.doneNumber !== 0) return true
        }
      },
      set(value) {
        this.doneAll(value)
      },
    },
  },
  methods: {
    ...mapActions(['doneAll', , 'clear']),
  },
}
</script>

<style scoped>
/* Footer */
.todo-footer {
  height: 40px;
  line-height: 40px;
  padding-left: 6px;
  margin-top: 5px;
}
