import Vue from 'vue'
import Vuex from 'vuex'
import { nanoid } from 'nanoid'
Vue.use(Vuex)

const state = {
  ItemList: JSON.parse(localStorage.getItem('todos')) || [],
}
const mutations = {
  ADD(state, title) {
    const item = {
      id: nanoid(),
      title: title,
      completed: false,
    }
    state.ItemList.unshift(item)
    localStorage.setItem('todos', JSON.stringify(state.ItemList))
  },
  CHANGEHANDLER(state, id) {
    state.ItemList.forEach((todo) => {
      if (todo.id === id) {
        todo.completed = !todo.completed
      }
    })
    localStorage.setItem('todos', JSON.stringify(state.ItemList))
  },
  REMOVEITEM(state, id) {
    state.ItemList = state.ItemList.filter((todo) => {
      return todo.id !== id
    })
    localStorage.setItem('todos', JSON.stringify(state.ItemList))
  },
  DONEALL(state, value) {
    state.ItemList.forEach((todo) => {
      todo.completed = value
    })
    localStorage.setItem('todos', JSON.stringify(state.ItemList))
  },
  CLEAR(state) {
    state.ItemList = state.ItemList.filter((todo) => {
      return !todo.completed
    })
    localStorage.setItem('todos', JSON.stringify(state.ItemList))
  },
}

const actions = {
  add(context, title) {
    context.commit('ADD', title)
  },
  changeHandler(context, id) {
    context.commit('CHANGEHANDLER', id)
  },
  removeItem(context, id) {
    context.commit('REMOVEITEM', id)
  },
  doneAll(context, value) {
    context.commit('DONEALL', value)
  },
  clear(context) {
    context.commit('CLEAR')
  },
}

export default new Vuex.Store({
  state: state,
  mutations: mutations,
  actions: actions,
})
